import React, { useEffect } from "react";
import "./personality.css";

//  Posters
import beforesunrise from "../../movies/amorosa/beforesunrise.jpg";
import casablanca from "../../movies/amorosa/casablanca.jpg";
import inthemoodforlove from "../../movies/amorosa/inthemoodforlove.jpg";
import littlefish from "../../movies/amorosa/littlefish.jpg";
import neverletmego from "../../movies/amorosa/neverletmego.jpg";
import orgulloyprejuicio from "../../movies/amorosa/orgulloyprejuicio.jpg";
import paristexas from "../../movies/amorosa/paristexas.jpg";
import romanholiday from "../../movies/amorosa/romanholiday.jpg";
import thenotebook from "../../movies/amorosa/thenotebook.jpg";
import pastlives from "../../movies/amorosa/pastlives.jpg";

//  Datos reales
const movies = [
  {
    title: "Before Sunrise",
    image: beforesunrise,
    director: "RichardLinklater",
    year: 1995,
    cast: "EthanHawke, JulieDelpy",
    synopsis: "Dos jvenes se conocen en un tren y pasan una noche caminando por Viena explorando la conexin entre ellos."
  },
  {
    title: "Casablanca",
    image: casablanca,
    director: "MichaelCurtiz",
    year: 1942,
    cast: "HumphreyBogart, IngridBergman",
    synopsis: "Durante la Segunda Guerra Mundial, el dueo de un caf en Casablanca se reencuentra con un amor de su pasado entre intrigas y exilios."
  },
  {
    title: "In the Mood for Love",
    image: inthemoodforlove,
    director: "WongKarwai",
    year: 2000,
    cast: "TonyLeung, MaggieCheung",
    synopsis: "Un hombre y una mujer sospechan que sus parejas les son infieles y lentamente desarrollan una conexin silenciosa y profunda en el HongKong de los aos 60."
  },
  {
    title: "Little Fish",
    image: littlefish,
    director: "RowanWoods",
    year: 2005,
    cast: "CateBlanchett, HugoWeaving, SamNeill",
    synopsis: "Una mujer limpia de herona desde hace cuatro aos lucha por abrir su propio negocio mientras su pasado la acecha." /* :contentReference[oaicite:0]{index=0} */
  },
  {
    title: "Never Let Me Go",
    image: neverletmego,
    director: "MarkRomanek",
    year: 2010,
    cast: "CareyMulligan, KeiraKnightley, AndrewGarfield",
    synopsis: "Amistad, amor y destino trgico en un mundo distpico donde los seres humanos nacen para donar rganos."
  },
  {
    title: "Orgullo y Prejuicio",
    image: orgulloyprejuicio,
    director: "JoeWright",
    year: 2005,
    cast: "KeiraKnightley, MatthewMacfadyen",
    synopsis: "Una apasionada adaptacin del clsico de Jane Austen donde la joven Elizabeth Bennet y el seor Darcy deben superar sus prejuicios y orgullo."
  },
  {
    title: "Paris, Texas",
    image: paristexas,
    director: "WimWenders",
    year: 1984,
    cast: "HarryDeanStanton, NastassjaKinski",
    synopsis: "Un hombre que ha desaparecido reaparece aos despus para buscar una nueva conexin con su hijo y exesposa, explorando el sentido del hogar y de la memoria."
  },
  {
    title: "Roman Holiday",
    image: romanholiday,
    director: "WilliamWyler",
    year: 1953,
    cast: "AudreyHepburn, GregoryPeck",
    synopsis: "Una princesa escapa de sus obligaciones reales y conoce Roma con un periodista americano, viviendo una noche de libertad y romance."
  },
  {
    title: "The Notebook",
    image: thenotebook,
    director: "NickCassavetes",
    year: 2004,
    cast: "RyanGosling, RachelMcAdams",
    synopsis: "Un hombre lee la historia de un amor vivido dcadas atrs para revelarle a su esposa los recuerdos que comparten."
  },
  {
    title: "Past Lives",
    image: pastlives,
    director: "CelineSong",
    year: 2023,
    cast: "GretaLee, TeoYoo",
    synopsis: "Dos amigos de infancia se reencuentran tras aos separados y reflexionan sobre lo que pudo haber sido, el amor y el paso del tiempo."
  },
];

function Amorosa() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="personality-container">
      <h1>Amorosa</h1>
      <p>
        Las personas que no llegan a conocerte pueden tender a pensar que sos una persona poco amorosa,
        o poco romantica, pero una vez que empezas a entrar en confianza te das cuenta que con pequeos
        gestos, sos capaz de demostrar una cantidad enorme de amor, por amigos, familia, mascotas o quien sea.
      </p>
      <div className="movies-grid">
        {movies.map((movie) => (
          <div key={movie.title} className="movie-card">
            <div className="movie-card-inner">
              <div className="movie-card-front">
                <img src={movie.image} alt={movie.title} className="movie-poster" />
              </div>
              <div className="movie-card-back">
                <h3>{movie.title}</h3>
                <p><strong>Director:</strong> {movie.director}</p>
                <p><strong>Ao:</strong> {movie.year}</p>
                <p><strong>Reparto:</strong> {movie.cast}</p>
                <p><strong>Sinopsis:</strong> {movie.synopsis}</p>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Amorosa;


